th,
td {
    border: solid 1px green;
}

tr {
    margin: 0 auto;
    align-items: center;
    border: solid 2px red;
}

.wrapper-table-out .cover {
    grid-area: cover;
    border: solid 1px;
}

.wrapper-table-out .disc {
    grid-area: disc;
}

.wrapper-table-out .indef {
    grid-area: indef;
}